*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: 'GameBoy';
  src: local('GameBoy'), url(./fonts/GameBoy.woff) format('woff');
}

* {
  font-family: 'GameBoy', sans-serif;
}

html,
body,
#root,
.div-100vh {
  user-select: none;
  width: 100%;
  margin: 0;
  padding: 0;
  margin: 0;
}
